.group-form {
    position: relative;
}
.banner-tabs .group-form:before {
    content: '';
    background: #ff0000;
    width: 40px;
    height: 40px;
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 0;
	pointer-events: none;
}
.banner-tabs .group-form:after{
	border-bottom: 2px solid #999;
    border-right: 2px solid #999;
    content: '';
    display: block;
    height: 5px;
    margin-top: -4px;
    pointer-events: none;
    position: absolute;
    top: 50%;
    -webkit-transform-origin: 66% 66%;
            transform-origin: 66% 66%;
    -webkit-transform: rotate(
45deg);
            transform: rotate(
45deg);
    transition: all 0.15s ease-in-out;
    width: 5px;
	border-bottom-color: #fff;
    border-right-color: #fff;
    right: 18px;
	pointer-events: none;
}
.banner-tabs .group-form.input-group:after{
	display: none;
}
.group-form .custom-select:focus{
	background-color: #fff;
	border: 2px solid #ff0000;
}
