.accordion .card .collapseparent + .card-header .btn{
	background: #ededed;
    border-color: #ededed;
    color: #000;
}
.accordion .card .collapseparent + .card-header .btn::before{
	content: '\f067';
    background: #2d3e52;
    color: #ededed;
}
.accordion .card .collapseparent.show + .card-header .btn{
	background: #ff0000;
    color: #fff;
	border-color: transparent;
}
.accordion .card .collapseparent.show + .card-header .btn::before{
	background: #fff;
    color: #ff0000;
	content: '\f068';
}
